'use client';

// src/auth-client.ts
import { createClient } from "@supabase/supabase-js";
import { getBrandConfig } from "@act/tenant-config";
var AuthClient = class {
  constructor(brandId) {
    this.brand = getBrandConfig(brandId);
    this.supabase = createClient(
      this.brand.supabaseUrl,
      this.brand.supabaseAnonKey
    );
  }
  async signUp({ email, password, brandId, metadata = {} }) {
    const { data, error } = await this.supabase.auth.signUp({
      email,
      password,
      options: {
        data: {
          brand_id: brandId,
          ...metadata
        }
      }
    });
    return { data, error };
  }
  async signIn({ email, password }) {
    const { data, error } = await this.supabase.auth.signInWithPassword({
      email,
      password
    });
    return { data, error };
  }
  async signOut() {
    const { error } = await this.supabase.auth.signOut();
    return { error };
  }
  async resetPassword({ email }) {
    const { data, error } = await this.supabase.auth.resetPasswordForEmail(email, {
      redirectTo: `${window.location.origin}/auth/reset-password?brand=${this.brand.id}`
    });
    return { data, error };
  }
  async updatePassword(newPassword) {
    const { data, error } = await this.supabase.auth.updateUser({
      password: newPassword
    });
    return { data, error };
  }
  getUser() {
    return this.supabase.auth.getUser();
  }
  onAuthStateChange(callback) {
    return this.supabase.auth.onAuthStateChange(callback);
  }
  getBrand() {
    return this.brand;
  }
  getSupabaseClient() {
    return this.supabase;
  }
};
export {
  AuthClient
};
